import React from 'react'
import { loadAnimation } from "lottie-web";
import { defineLordIconElement } from "lord-icon-element";
import {Timer,Ingredients} from"../assets"
import { RiTimerFlashFill } from 'react-icons/ri';
import { BiFoodMenu } from 'react-icons/bi';
defineLordIconElement(loadAnimation);



function FoodTile( { recipe } ) {
  return (
    <div className='bg-[#FFE3D2] max-w-56 grid place-items-center h-96 p-5 overflow-hidden shadow-xl hover:shadow-lg rounded-md hover:shadow-[#ffa340] hover:scale-105  duration-500'
    onClick={()=>{
      window.open(recipe["recipe"]["url"]);
    }}
    >
        <img src={ recipe["recipe"]["image"] } className=' w-[200px] rounded-lg'/>
        <div >
           <p className=' text-start h-12 overflow-clip font-semibold text-[#401639]'>{recipe["recipe"]["label"]}</p>
           
        </div>
        <div className=' w-full flex gap-1 flex-col '>
              <p className='flex font-semibold gap-2 items-center text-[#401639] '>
              <RiTimerFlashFill/>
                Time : { recipe["recipe"]["totalTime"] }
              </p>
              <p className=' hidden sm:flex gap-2  sm:visible font-semibold items-center text-[#401639] '>
              <BiFoodMenu/>
                #Ingredients : { recipe["recipe"]["ingredients"].length } 
              </p>
        </div>
    </div>
  )
}

export default FoodTile