import React, { useState } from 'react';
import Axios from "axios";
import { loadAnimation } from "lottie-web";
import { defineLordIconElement } from "lord-icon-element";
import FoodTile from './FoodTile';
import {Cooking2,indian,american,breakfast,british,brunch,chinese,dinner,french,noFilter,invalidSearch,italian,japanese,lunch,mexican,snack,teaTime} from"../assets"
defineLordIconElement(loadAnimation);


function FoodDisplay( ) {
  const [query, setquery] = useState("");
  const [recipes, setrecipes] = useState([]);
  const [filter, setfilter] = useState("cusine");
  const [subFilter, setsubFilter] = useState();
  const [pgCounter, setpgCounter] = useState(0);
  const [nextUrl, setnextUrl] = useState("");
  const [prevUrl, setprevUrl] = useState("");
  const [invalidKey, setinvalidKey] = useState(false)
  const [cusine, setcusine] = useState("");
  const [mealType, setmealType] = useState("");
  const APP_KEY="c7315747eccba6712226fc7b31000d48	";
  const APP_ID="b80cdb94"
  var url =`https://api.edamam.com/api/recipes/v2?type=public&q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}${cusine}${mealType}`;
  
  const filters={
    cusine:["noFilter","american","british","mexican","indian","italian","chinese","japanese","french"],
    mealType:["noFilter","breakfast","brunch","lunch","snack","teatime","dinner"] 
  }
  const images ={
    "indian" :indian,
    "american":american,
    "british":british,
    "breakfast":breakfast,
    "brunch":brunch,
    "chinese":chinese,
    "dinner":dinner,
    "french":french,
    "italian":italian,
    "japanese":japanese,
    "lunch":lunch,
    "mexican":mexican,
    "snack":snack,
    "teatime":teaTime,
    "noFilter":noFilter
  }
  function handleFilter(event) {
    var filter=event.target.value;
    setfilter(filter);

    
  }
  function handleSubFilter(event) {
    var subFilter=event.target.value;
    setsubFilter(subFilter);
    switch(filter){
      case "cusine":
        if(subFilter==="noFilter"){
        setcusine("");
        break;
      }
         setcusine("&cuisineType="+subFilter); break;
      case "mealType" :
        if(subFilter==="noFilter"){
          setmealType("");
          break;
        }  
      setmealType("&mealType="+subFilter); break;
    }
    
  }
 function onSubmit(e){ 
  e.preventDefault();
  getRecipes(url);
  
 };

 async function getRecipes(search){
    var result= await Axios.get(search);
    if(result.data.count===0){
      setinvalidKey(true);
      setrecipes(result.data.hits);
    }
    else{
    setrecipes(result.data.hits);
    setinvalidKey(false);
    if(result.data._links.next){
      setnextUrl(result.data._links.next["href"]);
      setprevUrl(nextUrl);    
      
    }
    else{
      setnextUrl("")
    }
  }
    
    
  }
  
  return (
    <div className=' w-full h-screen' name="foodDisplay">
      
      <div className='w-full p-4 sm:flex  sm:flex-row  md:mb-10 justify-evenly items-end'>
          <div className=' h-28 max-w-[600px]   rounded-lg  flex-auto   flex align-middle justify-between items-center '>
          <div className=' flex-none'>
          <select id="underline_select" defaultValue="cusine" onChange={handleFilter} className="block  m-4 px-0 w-full text-sm text-[#401639] bg-transparent border-0 border-b-2 border-[#E97FA3] appearance-none  focus:outline-none focus:ring-0 focus:border-[#401639] peer">
              
              <option  value="cusine">Cusine</option>
              <option value="mealType">MealType</option>
          </select>        
            </div>
            <div className='mx-9  md:max-w-[330px] lg:max-w-[450px] max-h-24  '>
    <ul className="  h-24 md:flex md:flex-row md:h-full gap-4 w-full  overflow-auto whitespace-nowrap scrollbar-hide flex-1"
    
    >
          {filters[filter].map( filter =>
           {
            return(
           <li className=' my-2' key={filter} >
              <input type="radio" id={filter} name="hosting"onChange={handleSubFilter} value={filter} className="hidden peer" 
              checked={ filter===subFilter?true:"" } />
              <label htmlFor={filter} className="  grid  w-20 h-20  place-items-center   text-[#f0578a] bg-white rounded-lg border  border-[#f1598c] cursor-pointer  peer-checked:border-[#401639] peer-checked:text-[#401639] hover:text-[#ffa340] hover:bg-gray-100  "> 
              <img src ={images[filter]} alt={filter} className=" max-h-10" />                          
              <div className=" overflow-hidden  font-semibold">{filter}</div>
              </label>
            </li>
            )
            }) }   
         </ul>
            </div>
          </div>
          <form className='group h-28  w-full  sm:w-[15rem]  md:w-[20rem]   lg:w-[24rem]   text-white flex-none  sm:mx-9 lg:pl-9'
           onSubmit={onSubmit}
          >   
            <div className="relative  mt-4 md:mt-9  lg:mt-10">         
              <input type="search" id="default-search" className="block p-4  w-full text-sm text-gray-900 border-[#E97FA3] focus:outline-[#E97FA3] rounded-lg border-2    " 
                     placeholder="Search " 
                     required 
                     value={query}
                     onChange={(e)=>setquery(e.target.value)} 
              />
              <button type="submit" className=" group text-white absolute right-2.5 bottom-2.5 bg-[#FFB05C] hover:bg-[#ffa340] focus:ring-4 focus:outline-none focus:ring-[#ffa340] font-medium rounded-lg text-sm px-4 py-2 ">
                <lord-icon
                  src="https://cdn.lordicon.com/pvbutfdk.json"
                  trigger="hover"
                  target="button.group"
                  colors="primary:#ffffff"
                  style={{ width:"20px",height:"20px" }}>
                </lord-icon>
                
              </button>
            </div>  
          </form>
      </div>
      
     
      {// if recipeas eggsist
        recipes.length?
        <>
         <div className='w-full px-4 gap-8 grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 place-items-center  '>
        { recipes.map( recipe=>{
          return <FoodTile recipe={recipe}/>
        })}
      </div>
      <div className='flex justify-around p-4 mt-16 place-items-center'>
      <button type="submit" title={pgCounter?'Previous Page':'No Previous Page'} className="  group text-white     focus:outline-none bg-[#401639] font-medium rounded-lg text-sm px-2 py-1 "
      onClick={()=>{
        if(pgCounter){
        getRecipes(prevUrl);
        setpgCounter(pgCounter-1);
        
      }
      }}
      
      >
        <lord-icon
          src="https://cdn.lordicon.com/ememvspi.json"
          trigger="loop-on-hover"
          target="button.group"         
          style={{ width:"30px",height:"30px" }}>
          </lord-icon>            
      </button>
      
      <button type="submit" title={nextUrl?'Next Page':"No further pages"} className=" scale-x-[-1] group text-white focus:outline-none bg-[#401639] font-medium rounded-lg text-sm px-2 py-1 "
      onClick={()=>{
        if(nextUrl){
        setpgCounter(pgCounter+1);
        getRecipes(nextUrl);
        
        }
      }}
      
      >
        <lord-icon
          src="https://cdn.lordicon.com/ememvspi.json"
          trigger="loop-on-hover"
          target="button.group"         
          style={{ width:"25px",height:"25px" }}>
          </lord-icon>            
      </button>
      
      </div>
      </>
      : //if recipeas doughnut eggsit
      <div className='w-full h-[24rem] mt-1 md:mt-0 grid place-items-center'>
       <img src={invalidKey?invalidSearch:Cooking2} alt="food" className=" max-w-[16rem]" />
       <p className={`eading-normal text-xl max-w-[190px] ${invalidKey?"text-red-800":"text-[#401639]"} my-2 text-center`} >
            {invalidKey?"No matching result found!! Try some other Keyword":" What's Cooking on  Your Mind Today ?"}       </p>  
      </div>
      
      }
      <footer className='w-full h-14 md:h-9  mt-56 md:mt-20 bg-[#401639]  '>

      </footer>
  
    </div>
  )
      
}

export default FoodDisplay;