import React from 'react';
import {Cooking} from"../assets"
import { Link } from 'react-scroll';
import { IoFastFoodSharp } from 'react-icons/io5';
import { loadAnimation } from "lottie-web";
import { defineLordIconElement } from "lord-icon-element";
defineLordIconElement(loadAnimation);


function LandingPage() {
  return (
    <div className='bg-[#FFE3D2] w-full h-[75vh] rounded-b-3xl'>
        <div className=" w-[150px] h-[50px] bg-[#FF7268] rounded-br-lg font-sans font-bold flex gap-1 place-content-center place-items-center text-lg text-[#401639]  ">
          <IoFastFoodSharp />  foodie
        </div>
        <div className="h-full flex  justify-evenly items-center ">
        <div className='h-fit max-w-[600px]   flex-auto px-9 ml-5 mt-[-25px]'>
            <p className=" leading-tight text-3xl sm:text-5xl text-[#401639] ">
            Thinking of something to Cooking ?
            </p>
            <p className="leading-normal text-xl max-w-[190px] text-[#401639] my-2 ">
            Get the best recipes there is in a single click            </p>            
            <Link to="foodDisplay" spy={true} smooth={true} offset={50} duration={500}>
                <button className=' group border-2 px-3 py-1 my-2 flex items-center rounded-md hover:text-white hover:bg-[#401639] border-[#E97FA3] text-[#401639]'>
                  Get Started
                    <lord-icon
                      src="https://cdn.lordicon.com/qothfipj.json"
                      trigger="hover"
                      target="button.group"
                      style={{ width:"35px",height:"35px" }}
                  ></lord-icon>
                </button>
            </Link>
            
        </div>
        <div className='h-fit  w-fit   text-white flex-none '>
            <img src={Cooking}className=" h-0 sm:h-[20rem] sm:w-[20rem] md:h-[25rem] md:w-[25rem]  md:mt-8 lg:w-[26.5rem] lg:h-[26.5rem] " />
        </div>
       
        </div>

    </div>
  );
}

export default LandingPage;